//  normalize styles
@use './normalize';
// utils

// page styles
@use "./pages/homepage.styles";
// component styles
@use "./components/header/header.styles";
@use "./components/mobileNav/mobileNav.styles";
@use "./components/logo/logo.styles";

html {
  height: 100%;
  width: 100%;
  background-color: white;
}

.App {
  font-family: 'Roboto';
  height: 100%;
  width: 100%;

  a {
    text-decoration: none;
    color: unset;
  }
  overflow: hidden; /* Hide horizontal scrollbar */
  & > img {
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -o-user-select: none;
    user-select: none;
  }
  ul {
    margin: 0;
  }
}

// .suspense-container {
//   height: 100vh;
//   background-color: black;
// }

body {
  &::-webkit-scrollbar {
    display: none;
  }
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
